import React, { useEffect, useState, useRef } from "react";



import { getStore, updStore, gen16CodeId,  ObjectKeys, fetchEncryptStream, convertArray2Obj, convertArray2ObjGroupby, hasWritePrivilege } from "../../../stores/Utils";

import {SearchInput_Cmpt, TabsHRM_Cmpt } from "../../../components";

import { OpenModal } from "../../../components/DialogHRM";

import "./dashboard.css"
import {  ProductItem_Cmpt, AddEntryProduct_Cmpt, AddOutProduct_Cmpt } from "..";
import {  getall_movement, search_movement, } from "../services/server_fetching";
import { get_balances_by_type } from "../../CuentasContables/services/server_fetching";
import MovementItem from "./MovementItem";



const SearchInput = SearchInput_Cmpt();


const ProductItem = ProductItem_Cmpt();


const AddEntryProduct = AddEntryProduct_Cmpt();
const AddOutProduct = AddOutProduct_Cmpt();



const MovementsDashboard  = ({ data, elmId, updObs }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);
	const [view, set_view] = useState(0);
	const [loading, set_loading] = useState(false);




	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			set_obs(gen16CodeId())
			getall_movement(cb_result);
			get_balances_by_type("global_accounts","global_account_params",updObs);
		}
	});


	const openUpdateView =  () => {
		
	}


	




	const handleChange =  (v) => {
		!loading && set_loading(true)
	}


	const handle_search_movement =  (v) => {
		set_loading(true)
		search_movement(cb_result)
	}


	const handle_getall_movement =  (v) => {
		getall_movement(cb_result);
	}

	const cb_result = async (f, v) => {
		set_obs(gen16CodeId())
		set_loading(false);
	};


	const openAddEntry =  () => {
		let dataModal = {};
		dataModal["zIndex"] = 450;
		dataModal["height"] = "80vh";
		// data['observeResize']=true;
		dataModal["props"] = { minHeight: "1vh", overlay: true };
		dataModal["content"] = 
			<AddEntryProduct />
		
		OpenModal(dataModal);
	}


	const openOutView =  () => {
		let dataModal = {};
		dataModal["zIndex"] = 450;
		dataModal["height"] = "80vh";
		// data['observeResize']=true;
		dataModal["props"] = { minHeight: "1vh", overlay: true };
		dataModal["content"] = 
			<AddOutProduct />
		
		OpenModal(dataModal);
	}



	return (

	<div className="">
		<div className="accounts_header">
			<h2>Movements</h2>
			<div className="_dsplFlx ">
				
				{hasWritePrivilege()?
				<p className="group_title addBtn purple" onClick={openAddEntry}>
					Entradas
				</p>
				:null}
				{hasWritePrivilege()?
				<p className="group_title addBtn purple" onClick={openOutView} >
					Salidas
				</p>
				:null}
			</div>
		</div>
		<div className="_dsplFlx " style={{marginTop:14}} >
			<SearchInput
				placeholder={"Buscar ...."} 
				width={560}
				obs={set_obs}
				fetchData={handle_search_movement}
				loading={loading}
				change={handleChange}
				callempty={handle_getall_movement}
				keyFlds={"searchMovQry"}
			/>
			<div className="flexSpace"/>
			<div className="_dsplFlx ">
				<div className="total_comprobante_title">
					No de Movientos: 
				</div>
				<div className="total_comprobante gray">
					<span>
						{ObjectKeys(getStore("movements_list"))?.length}
					</span>
				</div>
				
			</div>
		</div>
		<div className="movements_container scollVh">
			{getStore("movements_list") && ObjectKeys(getStore("movements_list"))?.map(cid=>{
				return (
					<MovementItem 
						data={getStore("movements_list")[cid]} 
						elmId={cid} 
						key={cid}
						openUpdateView={openUpdateView}
						handleRefreshAll={handle_getall_movement}
					/>
				)
			})}
		</div>
	</div>
	);
};

export default MovementsDashboard;


